import React from 'react';
import { render } from 'react-dom';
import FinderMini from './patterns/finder/finder-mini';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    if (/(Trident|MSIE)/.test(navigator.userAgent)) {
        return;
    } else {
        Array.from(document.querySelectorAll('.finder__mini')).forEach(
            finder => {
                finderConfig(finder.dataset.config).then(config => {
                    render(<FinderMini config={config}/>, finder);
                });
            }
        );
    }
});
