import React, { useState, useEffect } from 'react';
import { suggest } from '../../funnelback';
import PropTypes from 'prop-types';
import Clear from './finder__clear';

const maximumSuggestions = 5,
    keyCodeEscape = 27,
    keyCodeUp = 38,
    keyCodeDown = 40;

function highlightQueryTerm(suggestion, partialQuery) {
    return (
        <React.Fragment>
            <span>
                <span className="sr-only">{suggestion}</span>
                <span aria-hidden="true">
                    <span className="finder__suggestion">
                        {suggestion.slice(
                            suggestion.indexOf(partialQuery.toLowerCase()),
                            partialQuery.length
                        )}
                    </span>
                    {suggestion.slice(
                        suggestion.indexOf(partialQuery.toLowerCase()) +
                            partialQuery.length
                    )}
                </span>
            </span>
        </React.Fragment>
    );
}

/**
 * Search input field and autocomplete.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__query(props) {
    // save what they're typing
    const [partialQuery, setPartialQuery] = useState(props.query.query);
    // Funnelback suggestions for the currently typed text
    const [suggestions, setSuggestions] = useState([]);
    // Request token for calls to the Funnelback suggestions service, so we
    // can cancel it
    const [call, setCall] = useState({ cancel: () => {} });
    // ref for the input field, so we can .focus() it
    const stringLength = 16,
        stringOffset = -4,
        [inputId] = useState(
            'finder--' +
                props.query.collection +
                '--' +
                Math.random().toString(stringLength).slice(stringOffset)
        );
    // boolean to show or hide suggestions
    const [showSuggestions, setShowSuggestions] = useState(false);

    useEffect(() => {
        setPartialQuery(props.query.query);
    }, [props.updating]);

    let textInput = null;
    function focusInput() {
        textInput.focus();
    }

    // on clear, make a default request for results
    const clearQuery = () => {
        call.cancel();
        setPartialQuery('');
        setSuggestions([]);
        focusInput();

        const newQuery = props.query;
        newQuery.query = '';
        newQuery.sortType = props.config.sort[0].type;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const submitForm = () => {
        let queryURL = '';
        let finderMiniElement = document.getElementsByClassName(
            'finder__mini'
        )[0];
        let courseLevel = finderMiniElement.getAttribute('data-level');
        let courseSubject = finderMiniElement.getAttribute('data-subject');
        let courseType = finderMiniElement.getAttribute('data-type');

        queryURL =
            'meta_level_sand=' +
            courseLevel +
            '&meta_subject_sand=' +
            courseSubject +
            '&meta_type_sand=' +
            courseType;

        window.location.replace(
            `https://web2020.city.ac.uk/prospective-students/courses?${queryURL}&query=${partialQuery}`
        );
    };

    const submitSuggestion = (s) => {
        call.cancel();
        const newQuery = props.query;
        newQuery.query = s;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    // render suggestions
    // TODO: probably should be refactored into a separate component
    const suggestionsList = suggestions && suggestions.length > 0 && (
        <ul
            className={
                showSuggestions
                    ? 'finder__query__suggestions show'
                    : 'finder__query__suggestions hide'
            }
        >
            {[...new Set(suggestions)]
                .slice(0, maximumSuggestions)
                .map((suggestion) => (
                    <li key={suggestion}>
                        <button
                            type="button"
                            onBlur={() => setShowSuggestions(false)}
                            onFocus={() => setShowSuggestions(true)}
                            onMouseDown={() => {
                                //for browsers because onBlur get excuted before onClick
                                setShowSuggestions(true);
                                setPartialQuery(suggestion);
                                setSuggestions([]);
                                focusInput();
                                submitSuggestion(suggestion); //passing suggestion because partialQuery value get overwritten for some reason.
                            }}
                            onClick={() => {
                                //for mobile
                                setShowSuggestions(true);
                                setPartialQuery(suggestion);
                                setSuggestions([]);
                                focusInput();
                                submitSuggestion(suggestion); //passing suggestion because partialQuery value get overwritten for some reason.
                            }}
                            onKeyDown={(e) => {
                                switch (e.keyCode) {
                                    case keyCodeEscape:
                                        e.target.parentNode.parentNode.parentNode
                                            .querySelector('input')
                                            .focus();
                                        setSuggestions([]);
                                        break;
                                    case keyCodeUp:
                                        if (
                                            e.target.parentNode
                                                .previousElementSibling &&
                                            e.target.parentNode.previousElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.previousElementSibling
                                                .querySelector('button')
                                                .focus();
                                        } else {
                                            e.preventDefault();
                                            e.target.parentNode.parentNode.parentNode
                                                .querySelector('input')
                                                .focus();
                                        }
                                        break;
                                    case keyCodeDown:
                                        e.preventDefault();
                                        if (
                                            e.target.parentNode
                                                .nextElementSibling &&
                                            e.target.parentNode.nextElementSibling.querySelector(
                                                'button'
                                            )
                                        ) {
                                            e.preventDefault();
                                            e.target.parentNode.nextElementSibling
                                                .querySelector('button')
                                                .focus();
                                        }
                                        break;
                                    default:
                                        break;
                                }
                            }}
                        >
                            {highlightQueryTerm(suggestion, partialQuery)}
                        </button>
                    </li>
                ))}
        </ul>
    );

    const input = (
        <div className="finder__query__input">
            <div className="finder__query__icon--wrapper">
                <span className="finder__icon fal fa-search"></span>
            </div>
            <label className="sr-only" htmlFor={inputId}>
                {`Search ${props.config.summariseAs.plural}`}
            </label>
            <input
                autoComplete="off"
                id={inputId}
                name="query"
                placeholder={props.config.placeholder}
                ref={(input) => {
                    textInput = input;
                }}
                type="text"
                value={partialQuery}
                onKeyDown={(e) => {
                    switch (e.keyCode) {
                        case keyCodeEscape:
                            clearQuery();
                            break;
                        case keyCodeDown:
                            if (suggestions && suggestions.length > 0) {
                                e.preventDefault();
                                e.target.parentNode
                                    .querySelector(
                                        '.finder__query__suggestions button'
                                    )
                                    .focus();
                            }
                            break;
                        default:
                            break;
                    }
                }}
                onFocus={() => setShowSuggestions(true)}
                onBlur={() => setShowSuggestions(false)}
                onChange={(e) => {
                    //clear old suggestions
                    setSuggestions([]);
                    // keep  what they're typing
                    setPartialQuery(e.target.value);

                    /**
                     * if we have a request to the suggestions service in progress,
                     * cancel it. Stops old suggestions overwriting new ones because
                     * the requests can't keep up with fast typing.
                     */
                    call.cancel();

                    if (e.target.value) {
                        // input is populated, ask for suggestions
                        const [suggestionsPromise, newCall] = suggest(
                            props.query.collection,
                            e.target.value
                        );

                        // update our request cancel function for the new request
                        setCall({
                            cancel: () => {
                                newCall.cancel();
                            },
                        });

                        suggestionsPromise
                            .then((data) => setSuggestions(data))
                            .catch(() => setSuggestions([]));
                    } else {
                        // input is empty, empty suggestions
                        setSuggestions([]);
                    }
                }}
            />
            {suggestionsList}
            {clear}
        </div>
    );

    return (
        <fieldset className="finder__query">
            <div>
                {input}
                <button
                    type="submit"
                    className="finder__query__submit"
                    onClick={() => submitForm()}
                >
                    <span
                        className="fal fa-search finder__query__submit__icon"
                        aria-hidden="true"
                    />{' '}
                    <span className="finder__query__submit__text">Find</span>
                </button>
            </div>
        </fieldset>
    );
}

finder__query.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default finder__query;
